import React, { Component } from 'react';
import { ChromePicker } from 'react-color';
import styled from 'styled-components';
import './App.css';

const Wrapper = styled.p`
text-align: center;
color: white;
font-size: 200px;
font-family: Arial, Helvetica, sans-serif;
background: transparent;
`;

const ColorPicker = styled.div.attrs({
  background: props => props.background || 'green',
  top: props => props.top,
})`
width: 100px;
height: 100px;
top: ${props => props.top};
right: 0;
width: 200px;
height: 100px;
position: absolute;
z-index: 100;
background: ${props => props.background};
`


const Test = styled.div.attrs({
  background: props => props.background || 'green',
})`
position: absolute;
height: 100%;
width: 100%;
background: ${props => props.background};
`;

class App extends Component {
  state = {
    color1: 'lightblue',
    color1h: '20',
    color2: '#F4C3FD',
    color2h: '80',
    degree: 30,
  };

  handleChange = (event) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }


  render() {
    return (
      <Test className="gradientr" background={`linear-gradient(${this.state.degree}deg, hsl(${this.state.color1h}, 90%, 50%), hsl(${this.state.color2h}, 90%, 50%))`}>
        <Wrapper>
          gradientr
          <input type="range" name="degree" value={this.state.degree} onChange={this.handleChange} />
          <ColorPicker top="80px" background={`hsl(${this.state.color1h}, 90%, 50%)`}/>
          <input type="range" min="0" max="360" name="color1h" value={this.state.color1h} onChange={this.handleChange} />
          <ColorPicker top="380px" background={`hsl(${this.state.color2h}, 90%, 50%)`}/>
          <input type="range" min="0" max="360" name="color2h" value={this.state.color2h} onChange={this.handleChange} />
        </Wrapper>
      </Test>
    );
  }
}

export default App;



